@charset "UTF-8";

.tab-menu {
    padding-left: 16px;
    padding-right: 16px;
    background-color: #fff;
    border-bottom: 1px solid #e1e1e1
}

.tab-menu:after,
.tab-menu:before {
    display: table;
    content: ""
}

.tab-menu:after {
    clear: both
}

.tab-menu .tab-item {
    float: left;
    margin-left: 2rem
}

.tab-menu .tab-item.on .link-block {
    color: #178bce
}

.tab-menu .tab-item.on .link-block:after {
    position: absolute;
    right: 0;
    bottom: -1px;
    left: 0;
    height: 3px;
    background-color: #178bce;
    overflow: hidden;
    content: ""
}

.tab-menu .tab-item .link-block {
    position: relative;
    padding-top: .2rem;
    display: inline-block;
    font-size: 1.4rem;
    line-height: 4.6rem;
    letter-spacing: -.01em;
    color: #5e5e5e;
    white-space: nowrap
}

.tab-menu .tab-item:first-of-type {
    margin-left: 0
}

.tab-menu.type2 .tab-item.on .link-block {
    color: #333
}

.tab-menu.type2 .tab-item.on .link-block:after {
    background-color: #333
}