@charset "UTF-8";

.header {
    position: relative;
    background-color: #fff
}

.header .gnb-btn-hamburger {
    position: absolute;
    top: 1.4rem;
    left: 1.2rem
}

.header .gnb-btn-hamburger .ico-hamburger {
    width: 24px;
    height: 24px;
    background-position: -428px -81px;
    background-size: 460px 428px
}

.header .logo-tworld-symbol {
    position: absolute;
    top: 1.2rem;
    left: 4.8rem
}

.header .logo-tworld-symbol .ico-tworld-symbol {
    width: 28px;
    height: 28px;
    background-position: -35px -375px;
    background-size: 460px 428px
}

.header .gnb-logo {
    padding: 1.8rem 7.6rem 1.3rem 7.6rem;
    height: 5.2rem;
    line-height: 0;
    text-align: center
}

.header .gnb-logo .logo-tworld {
    display: inline-block;
    vertical-align: middle
}

.header .gnb-logo .logo-tworld .ico-tworld {
    width: 42px;
    height: 14px;
    margin-top: 2px;
    background-position-x: -59px;
}

.header .gnb-logo .logo-direct,
.header .gnb-logo .logo-direct:before {
    display: inline-block;
    vertical-align: middle
}

.header .gnb-logo .logo-direct:before {
    width: 1px;
    height: 12px;
    background-color: #aeaeae;
    overflow: hidden;
    margin: 2px 7px 0 1px;
    content: ""
}

.header .gnb-logo .logo-direct .ico-direct {
    width: 63px;
    height: 14px;
    margin-top: 2px;
    background-position: -59px -15px;
}

.header .gnb-btn-my {
    position: absolute;
    top: 1.4rem;
    right: 1.2rem
}

.header .gnb-btn-my .ico-my {
    width: 24px;
    height: 24px;
    background-position: -428px -193px;
    background-size: 460px 428px
}

.header .lnb-wrap {
    position: relative;
    width: 100%;
    overflow: hidden
}

.header .lnb-wrap .lnb {
    z-index: 999;
    position: relative;
    display: block;
    padding-left: 16px;
    padding-right: 16px
}

.header .lnb-wrap .lnb:after,
.header .lnb-wrap .lnb:before,
.tab-list .swiper-tab-list:before,
.tab-list .swiper-tab-list:after {
    content: "";
    width: 16px
}

.header .lnb-wrap .lnb:before,
.tab-list .swiper-tab-list:before {
    position: absolute;
    top: 0;
    bottom: 1px;
    left: 0;
    z-index: 10;
    background: #fff;
    background: -webkit-gradient(linear, left top, right top, from(#fff), color-stop(hsla(0, 0%, 100%, .8)), to(hsla(0, 0%, 100%, 0)));
    background: linear-gradient(90deg, #fff, hsla(0, 0%, 100%, .8), hsla(0, 0%, 100%, 0))
}

.header .lnb-wrap .lnb:after,
.tab-list .swiper-tab-list:after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 3px;
    z-index: 10;
    background: hsla(0, 0%, 100%, 0);
    background: -webkit-gradient(linear, left top, right top, from(hsla(0, 0%, 100%, 0)), color-stop(hsla(0, 0%, 100%, .8)), to(#fff));
    background: linear-gradient(90deg, hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, .8), #fff)
}

.header .lnb-wrap .lnb-item {
    margin-left: 2.2rem;
    width: auto
}

.header .lnb-wrap .lnb-item .link-block[aria-current],
.swiper-tab-list .tab-list-item .link-block[aria-current] {
    font-weight: 700;
    color: #ef4b49
}

.header .lnb-wrap .lnb-item .link-block[aria-current]:after,
.swiper-tab-list .tab-list-item .link-block[aria-current]:after {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 3px;
    background-color: #ef4b49;
    overflow: hidden;
    content: ""
}

.header .lnb-wrap .lnb-item .link-block {
    position: relative;
    padding-bottom: .2rem;
    display: inline-block;
    font-size: 1.4rem;
    line-height: 5rem;
    letter-spacing: -.01em;
    color: #5e5e5e;
    white-space: nowrap
}

.header .lnb-wrap .lnb-item:first-of-type {
    margin-left: 0
}

.header .lnb-wrap:after {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    height: 1px;
    background-color: #e1e1e1;
    overflow: hidden
}

.header.header-sub .top-menu,
.sticky-top .top-menu {
    position: relative;
    padding: 1.1rem 16px 0;
    height: 5.2rem
}

.header.header-sub .top-menu .h-type {
    display: inline-block;
    font-size: 1.8rem;
    line-height: 1;
    color: #000;
    font-weight: 400;
    vertical-align: middle
}

.header.header-sub .btn-share {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 16px;
    padding: .5rem
}

.header.header-sub .btn-share .ico-share {
    width: 18px;
    height: 20px;
    background-position: -48px -408px;
    background-size: 460px 428px
}

.header.header-sub .my-info {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    padding-left: 16px;
    padding-right: 16px;
    line-height: 2.2rem
}

.header.header-sub .my-info .ct {
    -webkit-box-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    flex: 1 1;
    margin-right: 1rem;
    min-width: 0
}

.header.header-sub .my-info .ct .name {
    display: block;
    font-size: 1.8rem;
    font-weight: 400
}

.header.header-sub .my-info .ct .phone {
    display: block
}

.header.header-sub .my-tab:after {
    content: "";
    display: block;
    height: 11px;
    border-top: 1px solid #e1e1e1;
    background-color: #f5f5f5
}

.header.header-sub .my-tab .my-tab-list {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    -webkit-justify-content: space-between;
    -moz-justify-content: space-between;
    justify-content: space-between;
    padding: 2.2rem 0
}

.header.header-sub .my-tab .my-tab-item {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    position: relative;
    width: 0
}

.header.header-sub .my-tab .my-tab-item .link-block {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    width: 100%;
    height: 3rem;
    font-size: 1.3rem;
    color: #000
}

.header.header-sub .my-tab .my-tab-item .link-block .text-secondary {
    margin-left: .3rem;
    font-weight: 700
}

.header.header-sub .my-tab .my-tab-item:before {
    margin: -1px .6rem 1px .2rem;
    display: inline-block;
    width: 1px;
    height: 1.2rem;
    background-color: #d1d1d1;
    overflow: hidden;
    vertical-align: middle;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    left: 0;
    margin: 0;
    content: ""
}

.header.header-sub .my-tab .my-tab-item:first-of-type:before {
    display: none
}

.btn-back {
    margin: 0 .2rem 0 -.6rem;
    position: relative;
    display: inline-block;
    width: 3rem;
    height: 3rem;
    vertical-align: middle
}

.btn-back .ico-back {
    width: 18px;
    height: 16px;
    background-position: -358px -375px;
    background-size: 460px 428px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto
}

.header-sticky .header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10000
}

.header-sticky .content {
    padding-top: 6.2rem
}