@charset "UTF-8";

.c-score {
    background-position: -74px -254px
}

.c-score,
.c-score .score {
    width: 73px;
    height: 13px;
    background-size: 460px 428px
}

.c-score .score {
    background-position: -151px -254px
}

.c-score-lg {
    width: 130px;
    height: 21px;
    background-position: -74px -204px;
    background-size: 460px 428px
}

.c-score-lg:after,
.c-score-lg:before {
    display: table;
    content: ""
}

.c-score-lg:after {
    clear: both
}

.c-score-lg .score {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 130px;
    height: 21px;
    background-position: -74px -229px;
    background-size: 460px 428px
}

.c-score-lg button[class^=btn-score] {
    float: left;
    position: relative;
    z-index: 1;
    width: 26px;
    height: 21px;
    opacity: 0
}

.c-score,
.c-score-lg {
    display: inline-block;
    position: relative;
    vertical-align: top
}

.c-score .score,
.c-score-lg .score {
    display: block;
    width: 0
}

.c-score .score.score1,
.c-score-lg .score.score1 {
    width: 20%
}

.c-score .score.score2,
.c-score-lg .score.score2 {
    width: 40%
}

.c-score .score.score3,
.c-score-lg .score.score3 {
    width: 60%
}

.c-score .score.score4,
.c-score-lg .score.score4 {
    width: 80%
}

.c-score .score.score5,
.c-score-lg .score.score5 {
    width: 100%
}

.c-review .review-filter,
.c-review .review-list,
.c-review .review-product {
    margin-left: 16px;
    margin-right: 16px
}

.c-review .btn-filter {
    position: relative;
    display: block;
    width: 100%;
    padding: 1.9rem 3rem 1.8rem 0;
    color: #000;
    font-size: 1.5rem;
    font-weight: 700;
    text-align: left;
    border: 1px solid #e1e1e1;
    border-width: 1px 0
}

.c-review .btn-filter:before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 17px;
    height: 17px;
    background-position: -376px -408px;
    background-size: 460px 428px;
    margin: auto 0;
    content: ""
}

.c-review .review-init {
    padding: 4rem 0;
    text-align: center
}

.c-review .review-init .text {
    color: #000;
    font-size: 1.4rem
}

.c-review .review-init .dsc {
    margin-top: 1.3rem;
    color: #818181;
    font-size: 1.2rem;
    line-height: 1.3
}

.c-review .review-list-item {
    position: relative;
    border-bottom: 1px solid #e1e1e1
}

.c-review .review-list-item .bar {
    display: inline-block;
    overflow: hidden;
    width: 0;
    height: 1.2rem;
    margin: 0 3px;
    color: transparent;
    border-left: 1px solid #e1e1e1;
    vertical-align: middle
}

.c-review .review-list-item.fold .review-head .product .btn-extra {
    display: none
}

.c-review .review-list-item.fold .review-head .product .text {
    max-width: 100%;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis
}

.c-review .review-list-item.fold .review-head .title {
    max-width: 70%;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis
}

.c-review .review-list-item.fold .btn-toggle-review .ico-toggle {
    width: 24px;
    height: 24px;
    background-position: -428px -165px;
    background-size: 460px 428px
}

.c-review .review-list-item.fold .review-body {
    display: none
}

.c-review .review-head {
    overflow: hidden;
    position: relative;
    padding: 1.3rem 3rem 1.2rem 0
}

.c-review .review-head .product {
    min-height: 2.7rem
}

.c-review .review-head .product .text {
    max-width: 70%;
    color: #000;
    font-size: 1.4rem;
    line-height: 1.7;
    word-break: break-all
}

.c-review .review-head .product .btn-sm {
    padding: .1rem .8rem 0;
    font-size: 1rem;
    line-height: 1.8rem
}

.c-review .review-head .nick,
.c-review .review-head .title {
    display: inline;
    color: #464646;
    font-size: 1.3rem;
    vertical-align: middle
}

.c-review .review-head .title {
    max-width: 70%;
    word-break: break-all
}

.c-review .review-head .btn-toggle-review {
    position: absolute;
    top: 2.3rem;
    right: 0;
    width: 24px;
    height: 24px
}

.c-review .review-head .btn-toggle-review .ico-toggle {
    width: 24px;
    height: 24px;
    background-position: -428px -53px;
    background-size: 460px 428px
}

.c-review .review-cont {
    margin-top: -.6rem;
    padding-bottom: 1.8rem
}

.c-review .review-cont .c-score {
    position: relative;
    bottom: -.2rem
}

.c-review .review-cont .date {
    color: #959494;
    font-size: 1.2rem
}

.c-review .review-cont .link-shop {
    color: #959494;
    font-size: 1.1rem;
    text-decoration: underline
}

.c-review .review-cont .review {
    overflow: hidden;
    width: 100%;
    margin-top: .7rem;
    color: #000;
    font-size: 1.3rem;
    line-height: 1.5;
    word-break: break-all
}

.c-review .review-reply {
    position: relative;
    margin-top: -.6rem;
    padding: 0 0 1.3rem 1.7rem
}

.c-review .review-reply:before {
    position: absolute;
    top: -2px;
    left: 0;
    width: 11px;
    height: 11px;
    border: 1px solid #d8d8d8;
    border-width: 0 0 1px 1px;
    content: ""
}

.c-review .review-reply .bar {
    height: 1rem
}

.c-review .review-reply .nick-admin {
    font-size: 1.2rem
}

.c-review .review-reply .date {
    color: #666;
    font-size: 1.2rem
}

.c-review .review-reply .reply {
    overflow: hidden;
    width: 100%;
    padding-top: .3rem;
    color: #757575;
    font-size: 1.3rem;
    line-height: 1.4;
    word-break: break-all
}

.c-review .review-product {
    padding-top: 2rem
}

.c-review .review-product .link-block {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center
}

.c-review .review-product .thumb {
    position: relative;
    display: block;
    width: 90px;
    min-width: 90px;
    height: 73px;
    margin-right: .6rem
}

.c-review .review-product .thumb img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%
}

.c-review .review-product .cont-area {
    -webkit-box-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    flex: 1 1;
    min-width: 0;
    padding-right: 2rem
}

.c-review .review-product .cont-area .date {
    display: inline-block;
    margin-bottom: 1rem;
    color: #8d8d8d;
    font-size: 1.2rem
}

.c-review .review-product .cont-area .product {
    color: #000;
    font-size: 1.5rem
}

.c-review .review-product .cont-area .info {
    display: block;
    margin-top: .2rem;
    color: #8d8d8d;
    font-size: 1.3rem
}

.c-review .review-product .cont-area .c-score {
    margin-top: .2rem
}

.c-review .review-product .btn-area {
    padding: 2rem 0
}

.c-review .btn-more-review {
    display: block;
    position: relative;
    padding: 1.7rem 3rem 1.6rem 16px;
    color: #5e5e5e;
    font-size: 1.3rem;
    background-color: #e6f2fb
}

.c-review .btn-more-review:before {
    position: absolute;
    top: 50%;
    right: 16px;
    width: 8px;
    height: 13px;
    background-position: -452px -221px;
    background-size: 460px 428px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    content: ""
}

.c-review .btn-more-product {
    margin-top: -1px
}

.c-review .review-write {
    margin-top: 2rem;
    border-top: 1px solid #e1e1e1
}

.c-review .review-write .score-area {
    padding: 2.8rem 0;
    text-align: center
}

.c-review .review-write .score-area .dsc {
    margin-top: 1rem;
    color: #212121;
    font-size: 1.3rem
}

.c-review .review-write .form-area .c-textarea {
    margin-top: .6rem;
    height: 15rem
}

.c-review .review-write .dsc-list {
    margin-top: 1rem
}

.c-review .review-write .dsc-list .dsc-item {
    line-height: 1.8rem
}

.c-review.v-my .review-head {
    padding-top: 1.8rem
}

.c-review.v-my .review-head .title {
    color: #212121;
    font-size: 1.4rem
}

.c-review.v-my .review-head .btn-toggle-review {
    top: 1.9rem
}

.c-review.v-my .review-list-item.fold .review-head .title {
    max-width: 90%
}