@charset "UTF-8";

.used-guide-box,
.used-page .c-link-bar,
.used-regist-page .c-link-bar,
.used-serial-page .c-link-bar {
    position: relative;
    border-top: 12px solid #f5f5f5
}

.used-guide-box:before,
.used-page .c-link-bar:before,
.used-regist-page .c-link-bar:before,
.used-serial-page .c-link-bar:before {
    position: absolute;
    top: -12px;
    right: 0;
    left: 0;
    height: 1px;
    background: #e1e1e1;
    content: ""
}

.used-page .l-fullscreen-fluid {
    position: relative;
    padding-bottom: 6rem
}

.used-page .l-fullscreen-fluid .btn-area-bottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%
}

.used-regist-wrap,
.used-search-wrap {
    margin-left: 16px;
    margin-right: 16px
}

.used-title {
    margin-top: 1.4rem;
    font-size: 1.4rem;
    font-weight: 400
}

.used-title .keyword {
    font-weight: 700
}

.used-search {
    padding-bottom: 5rem
}

.used-search .form-group {
    margin-top: .9rem
}

.used-search .search-dsc {
    margin-top: 1.1rem;
    color: #757575;
    font-size: 1.2rem
}

.used-search .link-area {
    margin-top: 1.7rem
}

.used-guide:after,
.used-guide:before {
    display: table;
    content: ""
}

.used-guide:after {
    clear: both
}

.used-guide-item {
    float: left;
    position: relative;
    width: 33%;
    padding: .8rem 0 3.8rem;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

.used-guide-item:before {
    display: block;
    margin: 0 auto;
    content: ""
}

.used-guide-item:after {
    position: absolute;
    top: 1.9rem;
    left: 0;
    width: 9px;
    height: 9px;
    margin-left: -9px;
    border-top: 1px solid #9e9e9e;
    border-right: 1px solid #9e9e9e;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    content: ""
}

.used-guide-item:first-of-type:before {
    width: 32px;
    height: 32px;
    background-position: -200px -334px;
    background-size: 460px 428px
}

.used-guide-item:first-of-type:after {
    display: none
}

.used-guide-item:nth-of-type(2):before {
    width: 32px;
    height: 32px;
    background-position: -308px -334px;
    background-size: 460px 428px
}

.used-guide-item:nth-of-type(3):before {
    width: 32px;
    height: 32px;
    background-position: -164px -334px;
    background-size: 460px 428px
}

.used-guide-item .dsc {
    margin-top: 1.2rem;
    color: #757575;
    font-size: 1.2rem;
    line-height: 1.4
}

.used-guide-box {
    margin-top: 0;
    padding: 3rem 16px 2.5rem
}

.used-guide-box .dsc {
    color: #757575;
    font-size: 1.1rem;
}

.used-guide-box .dsc em {
    display: block;
    margin-bottom: .4rem;
    color: #000;
    font-size: 1.5rem
}

.used-guide-box .link {
    display: inline-block;
    margin-top: 1.2rem;
    color: #3b98e6;
    font-size: 1.1rem
}

.used-guide-box .link,
.used-guide-box .link:active,
.used-guide-box .link:hover,
.used-guide-box .link:visited {
    text-decoration: underline
}

.used-search-result {
    margin-top: -2.1rem;
    border-top: 1px solid #e1e1e1
}

.used-search-result .title-result {
    padding: 1.8rem 0 2rem;
    font-size: 1.4rem
}

.used-search-result .title-result .text {
    font-weight: 400
}

.used-search-result .title-result .point {
    color: #f76252
}

.used-search-result .result-none {
    padding: 3rem 0 5.5rem;
    color: #818181;
    font-size: 1.3rem;
    text-align: center
}

.used-search-result .result-item {
    border-top: 1px solid #e1e1e1
}

.used-search-result .result-item:first-of-type {
    border-top: 0
}

.used-search-result .result-item .c-ick {
    display: block
}

.used-search-result .result-item .c-ick label {
    padding: 1.9rem 0 2rem 3.4rem
}

.used-search-result .result-item .c-ick label:before {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%)
}

.used-search-result .result-item .keyword-wrap {
    margin-right: 3rem;
    color: #202020;
    font-size: 1.3rem;
    font-weight: 400
}

.used-search-result .result-item .info {
    display: block;
    margin-top: .5rem;
    color: #757575;
    font-size: 1.2rem
}

.used-search-result .result-item .bar {
    display: inline-block;
    overflow: hidden;
    width: 0;
    height: 1.2rem;
    margin: -1px 6px 1px 4px;
    color: transparent;
    border-left: 1px solid #d1d1d1;
    vertical-align: middle
}

.used-regist-wrap {
    padding-bottom: 3rem
}

.used-regist-item {
    position: relative
}

.used-regist-item .title {
    display: block;
    padding: 3.3rem 0 .9rem;
    font-size: 1.5rem;
    line-height: 1;
    font-weight: 400
}

.used-regist-item .title .ico-require {
    position: relative;
    top: -.2rem
}

.used-regist-item .side {
    position: absolute;
    top: 2.9rem;
    right: 0
}

.used-none-serial {
    width: 248px;
    margin: 1rem auto;
    text-align: center
}

.used-none-serial .dsc {
    color: #000;
    font-size: 1.2rem;
    margin-bottom: 1rem
}