@charset "UTF-8";

.popup-tgift .dsc-list {
    margin-top: 4rem
}

.popup-tgift-detail .popup-body {
    padding-top: 0
}

.popup-tgift-detail .popup-body .popup-header {
    position: relative;
    padding: 1rem 16px 0;
    height: 5.1rem
}

.popup-tgift-detail .popup-body .popup-header .btn-back {
    float: none
}

.popup-tgift-detail .popup-body .popup-header .popup-title {
    margin-right: 0;
    display: inline-block;
    font-size: 1.8rem;
    line-height: 1;
    color: #000;
    font-weight: 400;
    vertical-align: middle
}

.popup-tgift-detail .popup-body .popup-content {
    padding-bottom: 7.2rem
}

.tgift-dsc {
    margin-top: 1.7rem;
    color: #212121;
    font-size: 1.3rem
}

.tgift-dsc-secondary {
    margin-top: .6rem;
    color: #757575;
    font-size: 1.2rem;
    line-height: 1.5
}

.tgift-title {
    padding: 1.3rem 0 2.3rem;
    color: #000;
    font-size: 2rem;
    line-height: 1.3;
    font-weight: 700;
    border-bottom: 1px solid #e1e1e1
}

.tgift-sub-title {
    margin-top: 2.5rem;
    color: #000;
    font-size: 1.5rem;
    font-weight: 400
}

.tgift-list {
    overflow: hidden;
    margin-top: 1.7rem
}

.tgift-list-item {
    float: left;
    width: 50%;
    margin-bottom: -1px;
    padding-top: 1.2rem;
    padding-bottom: 1.8rem;
    border: 1px solid #e1e1e1;
    border-width: 0 1px 1px 0
}

.tgift-list-item:nth-child(odd) {
    padding-left: .8rem;
    padding-right: 2.1rem
}

.tgift-list-item:nth-child(2n) {
    padding-right: .8rem;
    padding-left: 2.1rem;
    border-right-width: 0
}

.tgift-list-item .link-block {
    display: block
}

.tgift-list-item .thumb {
    position: relative;
    width: 13.5rem;
    height: 12.2rem;
    margin: 0 auto
}

.tgift-list-item .thumb img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-width: 100%;
    max-height: 100%;
    margin: auto
}

.tgift-list-item .title {
    overflow: hidden;
    display: block;
    display: -webkit-box;
    height: 39.2px;
    font-size: 1.4rem;
    line-height: 1.4;
    text-overflow: ellipsis;
    word-break: break-all;
    overflow-wrap: break-word;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-top: 4px;
    color: #000
}

.tgift-detail-wrap {
    padding: 1.3rem 1rem 4rem
}

.tgift-detail-wrap img {
    max-width: 100%
}