@charset "UTF-8";

.byadminn-detail-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    position: relative;
    max-width: 600px;
    height: 100%;
    min-height: 100%;
    margin: 0 auto
}

.byadmin-detail-header {
    padding: 1.2rem 16px;
    border-top: 5px solid #f76252;
    border-bottom: 1px solid #e6e6e6
}

.byadmin-detail-header .title {
    color: #000;
    font-size: 2.4rem;
    line-height: 1.1;
    font-weight: 300
}

.byadmin-detail-header .info {
    display: block;
    margin-top: .2rem;
    color: #8d8d8d;
    font-size: 1.4rem
}

.byadmin-detail-content {
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    height: 0;
    padding: 16px 0
}