@charset "UTF-8";

.agreeCon,
.c-term {
    color: #757575;
    font-size: 1.3rem;
    line-height: 1.4
}

.agreeCon em,
.agreeCon h2,
.agreeCon h3,
.agreeCon h4,
.agreeCon h5,
.agreeCon h6,
.c-term em,
.c-term h2,
.c-term h3,
.c-term h4,
.c-term h5,
.c-term h6 {
    font-size: 1.3rem;
    color: #000;
    font-weight: 400
}

.agreeCon h2:first-child,
.agreeCon h3:first-child,
.agreeCon ol:first-child,
.agreeCon p:first-child,
.agreeCon ul:first-child,
.c-term h2:first-child,
.c-term h3:first-child,
.c-term ol:first-child,
.c-term p:first-child,
.c-term ul:first-child {
    margin-top: 0
}

.agreeCon h2,
.c-term h2 {
    margin-bottom: 1.5rem
}

.agreeCon h2,
.agreeCon h3,
.c-term h2,
.c-term h3 {
    margin-top: 1.5rem
}

.agreeCon p,
.agreeCon ol,
.agreeCon table,
.agreeCon ul,
.c-term p,
.c-term ol,
.c-term table,
.c-term ul {
    margin-top: .6rem
}

.agreeCon .table-term,
.c-term .table-term {
    table-layout: fixed;
    width: 100%;
    border-top: 1px solid #e1e1e1
}

.agreeCon .table-term caption,
.c-term .table-term caption {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px)
}

.agreeCon .table-term td,
.agreeCon .table-term th,
.c-term .table-term td,
.c-term .table-term th {
    text-align: center;
    vertical-align: middle;
    border: 1px solid #e1e1e1;
    border-width: 0 0 1px 1px
}

.agreeCon .table-term td:first-child,
.agreeCon .table-term th:first-child,
.c-term .table-term td:first-child,
.c-term .table-term th:first-child {
    border-left: 0
}

.agreeCon .table-term th,
.c-term .table-term th {
    padding: .6rem 0;
    color: #5e5e5e;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 1.5;
    background-color: #fafafa
}

.agreeCon .table-term td,
.c-term .table-term td {
    padding: 1.2rem .4rem;
    color: #000;
    font-size: 1.2rem;
    line-height: 1.5
}

.agreeCon .table-term-row,
.c-term .table-term-row {
    table-layout: fixed;
    width: 100%
}

.agreeCon .table-term-row caption,
.c-term .table-term-row caption {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px)
}

.agreeCon .table-term-row td,
.agreeCon .table-term-row th,
.c-term .table-term-row td,
.c-term .table-term-row th {
    vertical-align: middle
}

.agreeCon .table-term-row th,
.c-term .table-term-row th {
    padding: .6rem 0;
    color: #5e5e5e;
    font-size: 1.2rem;
    font-weight: 400;
    text-align: center;
    line-height: 1.5;
    background-color: #fafafa;
    border: 1px solid #e1e1e1;
    border-width: 1px 1px 1px 0
}

.agreeCon .table-term-row td,
.c-term .table-term-row td {
    padding: 1.2rem .4rem 1.2rem 1rem;
    color: #000;
    font-size: 1.2rem;
    text-align: left;
    line-height: 1.5;
    border: 1px solid #e1e1e1;
    border-width: 1px 0
}

.agreeCon .point,
.c-term .point {
    color: #f76252;
    font-weight: 400
}

.agreeCon .table-term .align-left,
.agreeCon .table-term .left,
.c-term .table-term .align-left,
.c-term .table-term .left {
    text-align: left;
}

.agreeCon .link-txt,
.c-term .link-txt {
    color: #3E9AE6;
    text-decoration: underline;
}

.agreeCon strong,
.c-term strong {
    color: #333;
}

.agreeCon .agree-box,
.c-term .agree-box {
    display: table;
    width: 100%;
    table-layout: fixed;
    margin: 9px 0 18px;
    border: 1px solid #eaeaea;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.agreeCon .agree-box>p,
.agreeCon .agree-box>label,
.c-term .agree-box>p,
.c-term .agree-box>label {
    display: table-cell;
    padding: 7px 6px;
    vertical-align: middle;

}

.agreeCon .agree-box .agree-check,
.c-term .agree-box .agree-check {
    width: 100px;
    border-left: 1px solid #eaeaea;
    background-color: #eaeaea;
    color: #000;
    text-align: center
}

.agreeCon .agree-checkbox,
.c-term .agree-checkbox {
    margin-right: 5px;
    vertical-align: middle;
}

.agreeCon .txt-underline,
.c-term .txt-underline {
    text-decoration: underline;
}

.agreeCon .h-center,
.c-term .h-center {
    display: block;
    text-align: center;
}