@charset "UTF-8";

.error-wrap {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    width: 100%;
    height: 100%
}

.error-area {
    margin: 0 auto;
    padding: 2rem;
    max-width: 36rem;
    letter-spacing: -.025em;
    text-align: center
}

.error-area .error-title {
    font-size: 2.4rem;
    line-height: 1.3;
    color: #212121
}

.error-area .error-title:before {
    width: 80px;
    height: 80px;
    background-position: -84px 0;
    background-size: 460px 428px;
    margin: 0 auto 2.1rem;
    display: block;
    content: ""
}

.error-area .error-title .error-point {
    color: #f76252
}

.error-area .error-paragraph {
    margin-top: 1rem;
    font-size: 1.2rem;
    line-height: 1.5;
    color: #757575
}

.error-area .error-btn {
    margin: 17rem auto 0;
    display: block;
    width: 100%;
    max-width: 24.8rem;
    padding: 0 1.2rem;
    font-size: 1.4rem;
    line-height: 3.6rem;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    border-radius: 10em;
    background-color: #3b98e6;
    border: 0;
    cursor: pointer
}