@charset "UTF-8";

.dsc-item {
    font-size: 1.2rem;
    line-height: 1.4;
    color: #757575;
    letter-spacing: -.04em
}

.dsc-item .point {
    color: #178bce
}

.dsc-list .dsc-item {
    position: relative;
    padding-left: 1rem
}

.dsc-list .dsc-item:before {
    position: absolute;
    top: .6rem;
    left: .1rem;
    content: "";
    display: inline-block;
    width: 3px;
    height: 3px;
    background-color: #000;
    border-radius: 50%;
    overflow: hidden
}

.dsc-list .dsc-item~.dsc-item {
    margin-top: .5rem
}

.dsc-item-dot {
    position: relative;
    margin-top: .9rem;
    padding-left: 1rem;
    font-size: 1.2rem;
    line-height: 1.4;
    color: #757575
}

.dsc-item-dot:before {
    position: absolute;
    top: .7rem;
    left: .1rem;
    content: "";
    display: inline-block;
    width: 3px;
    height: 3px;
    background-color: #000;
    border-radius: 50%;
    overflow: hidden
}

.dsc-num-list {
    counter-reset: a
}

.dsc-num-list .dsc-num-item {
    position: relative;
    margin-top: .6rem;
    padding-left: 2.5rem;
    font-size: 1.2rem;
    line-height: 1.6rem;
    color: #757575
}

.dsc-num-list .dsc-num-item:first-of-type {
    margin-top: 0
}

.dsc-num-list .dsc-num-item:before {
    position: absolute;
    top: 1px;
    left: 0;
    counter-increment: a;
    content: counter(a, decimal-leading-zero);
    color: #a7a7a7
}

.fold-area {
    border-top: 1px solid #e1e1e1
}

.fold-area .fold-item {
    border-bottom: 1px solid #e1e1e1
}

.fold-area .fold-item.active .fold-tit .btn-toggle:after {
    width: 12px;
    height: 7px;
    background-position: -300px -179px;
    background-size: 460px 428px
}

.fold-area .fold-item.active .fold-con {
    display: block
}

.fold-area .fold-item .fold-tit {
    position: relative
}

.fold-area .fold-item .fold-tit .h-type {
    padding: 1.9rem 0;
    font-size: 1.5rem;
    color: #000;
    font-weight: 700;
    letter-spacing: -.03rem
}

.fold-area .fold-item .fold-tit .btn-toggle {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%
}

.fold-area .fold-item .fold-tit .btn-toggle:after {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 12px;
    height: 7px;
    background-position: -300px -168px;
    background-size: 460px 428px;
    right: 0;
    content: ""
}

.fold-area .fold-item .fold-con {
    display: none;
    padding-bottom: 3.2rem
}