@charset "UTF-8";

.product-list-area .c-banner,
.product-list-area .c-banner+.product-list {
	position: relative;
	border-top: 12px solid #f5f5f5
}

.product-list-area .c-banner+.product-list:before,
.product-list-area .c-banner:before {
	position: absolute;
	top: -12px;
	right: 0;
	left: 0;
	height: 1px;
	background: #e1e1e1;
	content: ""
}

.personal-msg {
	position: relative;
	border-bottom: 12px solid #f5f5f5
}

.personal-msg:before {
	position: absolute;
	right: 0;
	bottom: -1px;
	left: 0;
	height: 1px;
	background: #e1e1e1;
	content: ""
}

.personal-msg .link-block {
	display: block;
	position: relative;
	padding: 2.5rem 11rem 2.4rem 16px;
	background-color: #e6f2fb
}

.personal-msg .msg {
	color: #202020
}

.personal-msg .point {
	font-weight: 700
}

.personal-msg .more {
	position: absolute;
	top: 50%;
	right: 2rem;
	padding-top: .1rem;
	color: #eb4134;
	font-size: 1.3rem;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%)
}

.personal-msg .more.text-primary {
	color: #f76252
}

.filter-area {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	position: relative;
	width: 100%;
	border: 1px solid transparent;
	border-width: 0 1px
}

.filter-group {
	-webkit-box-flex: 1;
	-webkit-flex-grow: 1;
	-moz-flex-grow: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	width: 0;
	background-color: #fff;
	border: 1px solid #e1e1e1;
	border-width: 0 0 1px 1px
}

.filter-group:first-of-type {
	border-left: 0
}

.filter-group:first-of-type .filter-body {
	border-left: none
}

.filter-group:last-of-type .filter-body {
	border-right: none
}

.filter-group.product .filter-body {
	border-width: 0 0 1px
}

.filter-group.sort,
.filter-group.vendor {
	position: relative
}

.filter-group.sort .label:after,
.filter-group.sort.active .label:after {
	width: 14px;
	height: 14px;
	background-position: -353px -260px;
	background-size: 460px 428px
}

.filter-group.one .filter-head .text {
	display: inline-block;
	vertical-align: top
}

.filter-group.one .label:after {
	position: relative;
	top: -.1rem;
	right: auto;
	-webkit-transform: translateY(0);
	transform: translateY(0);
	display: inline-block;
	margin-left: .7rem
}

.filter-head {
	width: 100%
}

.filter-head .label,
.filter-head .sort-tit {
	display: block;
	position: relative;
	width: 100%;
	padding: .1rem 3.2rem 0 16px;
	color: #8d8d8d;
	font-size: 1.3rem;
	line-height: 4.8rem;
	text-align: left
}

.filter-head .label:after,
.filter-head .sort-tit:after {
	width: 10px;
	height: 7px;
	background-position: -301px -55px;
	background-size: 460px 428px;
	position: absolute;
	top: 50%;
	right: 1.5rem;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	content: ""
}

.active .filter-head .label:after,
.active .filter-head .sort-tit:after {
	width: 10px;
	height: 7px;
	background-position: -449px -387px;
	background-size: 460px 428px
}

.filter-head .label.selected,
.filter-head .sort-tit.selected {
	color: #000
}

.filter-head .sort-tit:after {
	display: none
}

.filter-head .text {
	display: block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis
}

.filter-body {
	position: absolute;
	top: 100%;
	right: -1px;
	left: -1px;
	z-index: 100;
	display: none;
	background-color: #fff;
	border: 1px solid #e1e1e1;
	border-width: 0 1px 1px
}

.filter-body .cont {
	padding: 0 2rem 2rem
}

.filter-body .title {
	margin-top: 2rem;
	color: #151d39;
	font-size: 1.4rem;
	font-weight: 700
}

.filter-body .btn-more {
	position: relative;
	width: 100%;
	margin-top: 1rem;
	padding: 0 3rem 0 1.4rem;
	color: #151d39;
	font-size: 1.4rem;
	line-height: 4rem;
	text-align: left;
	background-color: #f5f5f5;
	border-radius: 4px
}

.filter-body .btn-more:after {
	width: 17px;
	height: 17px;
	background-position: -355px -408px;
	background-size: 460px 428px;
	position: absolute;
	top: 50%;
	right: 1.4rem;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	content: ""
}

.filter-body .btn-more .text {
	max-width: 100%;
	display: inline-block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	max-width: 76%;
	vertical-align: middle
}

.filter-body .btn-more .badge,
.filter-body .btn-more .badge-round {
	margin: -.2rem 0 .2rem;
	vertical-align: middle
}

.filter-body .discount-option {
	margin-top: -.7rem
}

.filter-body .discount-option .c-ick {
	width: 49%;
	margin: 2rem 0 0;
	letter-spacing: -.04rem;
	white-space: nowrap
}

.filter-body .btn-secondary {
	margin-top: 1.8rem
}

.filter-group.active.product {
	border-bottom: 0
}

.filter-group.active .filter-body {
	display: block
}

.filter-list {
	padding: 8px 0 10px
}

.filter-list-item .item {
	width: 100%;
	padding: 0 16px;
	color: #5e5e5e;
	font-size: 1.3rem;
	text-align: left;
	line-height: 4rem
}

.product-list-dsc {
	margin-top: -1px;
	padding: 1rem 16px .8rem;
	border-top: 1px solid #e1e1e1
}

.product-list-dsc .dsc {
	color: #8d8d8d;
	font-size: 1.2rem
}

.filter-area+.product-list-area .product-list {
	margin-top: -1px
}

.product-list-area .init-area {
	border-top: 1px solid #e1e1e1;
	padding: 10rem 0
}

.product-list {
	padding: 0 16px
}

.product-list,
.product-list .product-list-item {
	border-top: 1px solid #e1e1e1
}

.product-list .product-list-item:first-of-type {
	border-top: none
}

.product-list .product-list-item .link-block {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	-moz-align-items: center;
	align-items: center;
	position: relative;
	padding: 21px 0
}

.product-list .product-list-item .thumb {
	position: relative;
	display: block;
	width: 130px;
	min-width: 130px;
	height: 130px;
	margin-right: 1.2rem
}

.product-list .product-list-item .thumb img {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	max-width: 100%;
	max-height: 100%
}

.product-list .product-list-item .thumb .badge {
	position: absolute;
	top: 5px;
	left: 2rem;
	overflow: hidden;
	max-width: 90px;
	padding-top: 0;
	padding-bottom: 0;
	line-height: 1.8rem;
	white-space: nowrap
}

.product-list .product-list-item .thumb .badge.rental {
	background-color: #f68744
}

.product-list .product-list-item .tag {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 5;
	width: 7rem;
	height: 2.8rem;
	margin: -1.4rem 0 0 -3.5rem;
	padding: .6rem 0;
	font-size: 1.2rem;
	color: #fff;
	font-weight: 700;
	text-align: center;
	background: rgba(0, 0, 0, .5)
}

.product-list .product-list-item .badge-area {
	position: absolute;
	top: 1.5rem;
	left: 0;
	z-index: 5
}

.product-list .product-list-item .badge-area .badge-half {
	display: block;
	margin-top: .2rem
}

.product-list .product-list-item .cont-area {
	-webkit-box-flex: 1;
	-moz-flex: 1;
	-ms-flex: 1;
	flex: 1 1;
	min-width: 0;
	padding-right: 1rem
}

.product-list .product-list-item .device {
	display: block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	color: #333;
	font-size: 1.6rem
}

.product-list .product-list-item .product-color {
	margin-top: .7rem
}

.product-list .product-list-item .product-color:after,
.product-list .product-list-item .product-color:before {
	display: table;
	content: ""
}

.product-list .product-list-item .product-color:after {
	clear: both
}

.product-list .product-list-item .product-color .color-chip {
	position: relative;
	display: block;
	float: left;
	width: 1rem;
	height: 1rem;
	margin-right: .6rem;
	color: transparent;
	font-size: 1px;
	line-height: 1px;
	border-radius: 100%;
	-webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, .4);
	box-shadow: inset 0 0 1px rgba(0, 0, 0, .4)
}

.product-list .product-list-item .option {
	margin-top: .7rem;
	font-size: 1.2rem;
	color: #8d8d8d
}

.product-list .product-list-item .option:after,
.product-list .product-list-item .option:before {
	display: table;
	content: ""
}

.product-list .product-list-item .option:after {
	clear: both
}

.product-list .product-list-item .option .giga {
	position: relative;
	display: block;
	float: left;
	padding: 0 .6rem;
	line-height: 1
}

.product-list .product-list-item .option .giga:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: .1rem;
	height: .9rem;
	background: #cfcfcf
}

.product-list .product-list-item .option .giga:first-of-type {
	padding-left: 0
}

.product-list .product-list-item .option .giga:first-of-type:before {
	display: none
}

.product-list .product-list-item .price {
	display: block;
	margin: 1.3rem 0 .5rem;
	color: #202020
}

.product-list .product-list-item .price .num,
.product-list .product-list-item .price .words {
	color: #f76252;
	font-size: 1.8rem;
	letter-spacing: -.03rem
}

.product-list .product-list-item .price .num {
	font-family: TgothicBlack, Roboto, sans-serif;
	font-weight: 700
}

.product-list .product-list-item .price .unit {
	position: relative;
	top: -.1rem;
	font-size: 1.3rem;
	font-weight: 400
}

.product-list .product-list-item .info,
.product-list .product-list-item .info2 {
	color: #8d8d8d;
	font-size: 1.2rem;
	line-height: 1.4;
	letter-spacing: -.02rem
}

.product-list .product-list-item .no-wrap {
	white-space: nowrap
}

.product-list .product-list-item .mark {
	display: block;
	overflow: hidden;
	margin-top: 13px;
	color: #178bce;
	font-size: 1.1rem
}

.product-list .product-list-item .banner-title {
	display: block;
	font-size: 1.5rem;
	color: #333;
	letter-spacing: -.05rem
}

.product-list .product-list-item .banner-info {
	margin-top: .4rem;
	color: #8d8d8d;
	font-size: 1.2rem;
	line-height: 1.5
}

.product-list .product-list-item.used-list-item {
	margin-left: -16px;
	margin-right: -16px;
	margin-bottom: -1px;
	border-top: 0
}

.product-list .product-list-item.used-list-item .link-block {
	padding: 0
}

.product-list .product-list-item.used-list-item .link-block .thumb {
	margin-right: 0;
	min-width: 0;
	width: 100%;
	height: auto
}

.product-list .product-list-item.used-list-item .link-block .thumb img {
	position: static;
	margin: 0;
	width: 100%;
	max-width: none;
	max-height: none;
	vertical-align: top
}