@charset "UTF-8";

.table-col-list,
.table-row-list {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    border-top: 1px solid #434159
}

.table-col-list caption,
.table-row-list caption {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px)
}

.table-col-list th,
.table-row-list th {
    border-color: #ddd;
    background-color: #f5f5f5;
    color: #000
}

.table-row-list td,
.table-row-list th {
    padding: 1.2rem 1.5rem;
    font-size: 1.3rem;
    line-height: 1.8rem;
    border-bottom: 1px solid #e1e1e1
}

.table-col-list {
    border-top-width: 2px
}

.table-col-list td,
.table-col-list th {
    padding: .6rem 0;
    font-size: 1.2rem;
    line-height: 1.8rem;
    border-left: 1px solid #e1e1e1;
    border-bottom: 1px solid #e1e1e1
}

.table-col-list th {
    text-align: center
}

.table-col-list td {
    padding: 1.2rem 0;
    text-align: center
}

.table-col-list td.al {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: left
}

.table-col-list tr td:first-child,
.table-col-list tr th:first-child {
    border-left: 0
}

.table-col-list .blank {
    height: 10rem;
    color: #999
}