@charset "UTF-8";

.popup {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10040;
    height: 100%;
    text-align: left;
    background-color: #fff
}

.popup .popup-body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    height: 100%;
    position: relative;
    margin: 0 auto;
    padding-top: 2rem;
    min-height: 100%;
    font-size: 1.3rem;
    line-height: 1.9rem
}

.popup .popup-header {
    padding: 0 16px 2rem;
    text-align: left
}

.popup .popup-header.align-c {
    text-align: center;
}

.popup .popup-header .popup-title {
    display: inline-block;
    margin-right: 3rem;
    color: #000;
    font-size: 1.8rem;
    line-height: 2.7rem;
    font-weight: 400;
    vertical-align: middle;
}

.popup .popup-content {
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    height: 0;
    padding: 0 16px 3rem
}

.popup .popup-footer .btn-area {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex
}

.popup .popup-footer .btn-area button[class^=btn-] {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    display: block;
    width: 100%;
    margin: 0;
    text-align: center
}

.popup .btn-close {
    position: absolute;
    top: 2.1rem;
    right: 2rem
}

.popup .btn-close .ico-close {
    width: 24px;
    height: 24px;
    background-position: -428px -137px;
    background-size: 460px 428px
}

.popup .c-link-bar {
    z-index: 1;
    margin: -.1rem -16px 0;
    border-top: 12px solid #f5f5f5;
    border-bottom: 1px solid #e1e1e1
}

.popup .c-link-bar:before {
    position: absolute;
    top: -12px;
    right: 0;
    left: 0;
    height: 1px;
    background: #e1e1e1;
    content: ""
}

.popup .popup-h-sub {
    margin-top: 3.6rem;
    color: #000;
    font-size: 1.3rem;
    font-weight: 700
}

.popup .popup-content .popup-h-sub:first-of-type {
    margin-top: 1.5rem
}

.popup .popup-dsc {
    margin-top: .4rem;
    color: #212121;
    font-size: 1.3rem;
    line-height: 1.6
}

.popup .popup-dsc-semi {
    margin-top: 1.1rem;
    color: #000;
    font-size: 1.2rem;
    line-height: 1.5
}