@charset "UTF-8";

.js-hide {
    display: none !important
}

.js-show {
    display: block !important
}

.js-noscroll {
    overflow: hidden !important;
}

.group-lr:after,
.group-lr:before {
    display: table;
    content: ""
}

.group-lr:after {
    clear: both
}

.group-l {
    float: left
}

.group-r {
    float: right
}

.row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    margin-left: -.3rem;
    margin-right: -.3rem
}

.row .col {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    margin: 0 .3rem;
    width: 0
}

.row .col~.col {
    text-align: right
}

.badge {
    display: inline-block;
    padding: .3rem .5rem .2rem;
    font-size: 1rem;
    line-height: 1;
    color: #fff;
    text-align: center;
    background-color: #8599c9;
    border: 0;
    border-radius: 2px;
    vertical-align: top
}

.badge.v1 {
    padding: .5rem .6rem;
    font-size: 1rem;
    border-radius: 10em
}

.badge.v1,
.badge.v2 {
    background-color: #f76252
}

.badge.clr1 {
    background-color: #fe795e
}

.badge.clr2 {
    background-color: #5bc09f
}

.badge.clr3 {
    background-color: #897fd7
}

.badge-round {
    display: inline-block;
    padding: .2rem .5rem 0;
    font-size: .9rem;
    line-height: 1.3rem;
    color: #fff;
    text-align: center;
    background-color: #52cd52;
    border: 0;
    border-radius: 1.5rem;
    vertical-align: top
}

.badge-round.type1 {
    background-color: #f6695c
}

.badge-round.type2 {
    background-color: #897fd7
}

.badge-half {
    display: inline-block;
    overflow: hidden;
    position: relative;
    width: 3.2rem;
    height: 3.2rem;
    padding: 0;
    background-color: #f6695c;
    color: #fff;
    font-size: 1rem;
    line-height: 1.1em;
    text-align: center;
    border-radius: 1rem;
    white-space: nowrap
}

.badge-half:before {
    width: 0;
    height: 100%;
    content: ""
}

.badge-half em,
.badge-half:before {
    display: inline-block;
    vertical-align: middle
}

.badge-half em {
    padding-top: .05rem;
    font-weight: 700;
    letter-spacing: -.04rem
}

.badge-half.type1 {
    background-color: #f6695c
}

.badge-half.type2 {
    background-color: #897fd7
}

.badge-half.type3 {
    background-color: #5bc09f
}

.badge-half.type4 {
    background-color: #178bce
}

.badge-half.type5 {
    background-color: #4a4a4a
}

.badge-soldout {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 5;
    min-width: 7rem;
    height: 2.8rem;
    padding: .6rem 0;
    font-size: 1.2rem;
    color: #fff;
    text-align: center;
    background: rgba(0, 0, 0, .5)
}

.horizontal-bar {
    content: "";
    display: block;
    height: 11px;
    border-top: 1px solid #e1e1e1;
    background-color: #f5f5f5
}

.ico-hot {
    width: 32px;
    background-position: -428px 0
}

.ico-help,
.ico-hot {
    height: 20px;
    background-size: 460px 428px
}

.ico-help {
    width: 20px;
    background-position: -24px -408px
}

.tag-item {
    display: inline-block;
    padding: .3rem .8rem .1rem;
    font-size: 1.2rem;
    line-height: normal;
    color: #489bdf;
    text-align: center;
    background: 0 0;
    border: 1px solid;
    border-radius: 10em;
    vertical-align: top
}

.text-link {
    display: inline-block;
    padding: .3rem 0;
    color: #178bce;
    font-size: 1.1rem
}

.text-link,
.text-link:active,
.text-link:hover,
.text-link:visited {
    text-decoration: underline
}

.text-primary {
    color: #f76252
}

.text-secondary {
    color: #178bce
}

.text-extra {
    color: #e6f2fb
}

.text-bold {
    font-weight: 700
}

.tab-list {
    position: relative;
}

.tab-list:after {
    position: absolute;
    right: 0;
    bottom: -1px;
    left: 0;
    z-index: 1;
    content: "";
    height: 1px;
    overflow: hidden;
    background-color: #e1e1e1
}

.tab-list .tab-list-item {
    display: inline-block;
    width: auto;
    margin-left: 2rem;
    color: #5e5e5e
}

.tab-list .tab-list-item:first-child {
    margin-left: 0;
}

.tab-list .tab-list-item .link-block {
    display: block;
    padding-bottom: 1.7rem;
    border-bottom: 2px solid transparent
}

.tab-list .swiper-tab-list {
    padding: 0 1.6rem;
}

.paginate {
    margin-top: 1rem;
    text-align: center
}

.paginate .pg-btn,
.paginate .pg-item {
    padding: 0 .5rem;
    display: inline-block;
    min-width: 3rem;
    height: 3rem;
    font-family: HelveticaNeue, Helvetica, sans-serif;
    font-size: 1.2rem;
    line-height: 3rem;
    color: #666;
    border: 1px solid #eee;
    border-radius: 4px;
    vertical-align: top
}

.paginate .pg-btn.active,
.paginate .pg-item.active {
    color: #178bce;
    font-weight: 700
}

.paginate .pg-btn {
    position: relative;
    border-color: #ddd
}

.paginate .pg-btn.prev:before {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%)
}

.paginate .pg-btn.next:before,
.paginate .pg-btn.prev:before {
    width: 7px;
    height: 12px;
    background-position: -449px -333px;
    background-size: 460px 428px;
    content: ""
}

.paginate .pg-btn.next:before {
    margin-top: -3px;
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
}

.ico-require {
    width: 8px;
    height: 8px;
    background-position: -452px -238px;
    background-size: 460px 428px
}

.c-tooltip {
    display: inline-block;
    vertical-align: top;
}

.c-tooltip .ico-tooltip {
    width: 14px;
    height: 14px;
    background-position: -400px -375px;
    background-size: 460px 428px
}

.c-tooltip .ico-tooltip.default {
    background-position: -414px -375px;
}

.sticky-shortcut {
    position: fixed;
    right: 2rem;
    bottom: 2rem;
    z-index: 10040
}

.sticky-shortcut .shortcut-menus {
    display: none
}

.sticky-shortcut [class^=btn-] {
    display: block;
    position: relative;
    width: 52px;
    height: 52px;
    margin-top: 14px;
    border-radius: 100%
}

.sticky-shortcut [class^=btn-] .text {
    position: absolute;
    top: 50%;
    right: 62px;
    font-size: 1.4rem;
    color: #fff;
    white-space: nowrap;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%)
}

.sticky-shortcut.active .shortcut-menus {
    display: block
}

.sticky-shortcut.active .btn-counsel-toggle .ico-counsel-toggle {
    width: 16px;
    height: 16px;
    background-position: -148px -104px;
    background-size: 460px 428px
}

.sticky-shortcut.active .btn-top:after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    border-radius: 100%;
    pointer-events: none;
    cursor: default;
    content: ""
}

.sticky-shortcut-dimmed {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10030;
    background-color: rgba(0, 0, 0, .5)
}

