.c-link-bar {
    position: relative
}

.c-link-bar .link-block {
    position: relative;
    display: block;
    width: 100%;
    padding: 2.3rem 16px 2.2rem;
    color: #5e5e5e;
    font-size: 1.5rem;
    line-height: 1;
    text-align: left
}

.c-link-bar .link-block:after {
    width: 16px;
    height: 16px;
    background-position: -380px -375px;
    background-size: 460px 428px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 16px;
    content: ""
}

.c-banner .link,
.c-img .link {
    display: block
}

.c-banner img,
.c-img img {
    max-width: 100%;
    width: 100%;
