@charset "UTF-8";

//---------------------------------------------------
// functions
//---------------------------------------------------
@function percent($current, $container) {
    @if $container {
        @return $current / $container * 100%;
    }

    @else {
        @error '$container is not defained';
    }
}

@function rem($current, $container: $root-font-size) {
    @if $container {
        @return $current / $container * 1rem;
    }

    @else {
        @error '$container is not defained';
    }
}

@function z($key) {
	@if not map-has-key($z-index, $key) {
		@warn "No layer found for `#{$key}` in $z-index map. Property omitted.";
	}

	@return map-get($z-index, $key);
}

//---------------------------------------------------
// mixins
//---------------------------------------------------
@mixin debug($color: red) {
    outline: 1px dashed $color;
}

@mixin debug-bg($color: red) {
    background-color: $color;
}

@mixin size($w: null, $h: $w) {
    width: $w;
    height: $h;
}

@mixin size-half($w: null, $h: $w) {
    width: $w / 2;
    height: $h / 2;
}

@mixin clear-fix {
    &::before,
    &::after {
        content: '';
        display: table;
    }
    &::after {
        clear: both;
    }
}

@mixin g-invisible {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	clip: rect(1px, 1px, 1px, 1px);
}

@mixin g-invisible-reset {
	position: static;
	width: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	clip: auto;
}

@mixin background-image($name, $repeat: no-repeat, $x: center, $y: top, $color: null) {
    background: $color url($name) $repeat $x $y;
}

@mixin position($position, $top: null, $right: null, $bottom: null, $left: null, $z-index:null) {
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    z-index: $z-index;
}

@mixin relative($args...) {
    @include position(relative, $args...);
}

@mixin absolute($args...) {
    @include position(absolute, $args...);
}

@mixin fixed($args...) {
    @include position(fixed, $args...);
}

@mixin for-screen($range) {
	$phone: 600px;              //480px  phone-upper-boundary
	$tablet: 900px;             //768px  tablet-portrait-upper-boundary
	$tablet-landscape: 1200px;  //992px  tablet-landscape-upper-boundary
	$desktop: 1800px;           //1200px desktop-upper-boundary

	@if $range == phone-only {
		@media (max-width: #{$phone - 1}) { @content; }
	} @else if $range == tablet {
		@media (min-width: $phone) { @content; }
	} @else if $range == tablet-landscape {
		@media (min-width: $tablet) { @content; }
	} @else if $range == desktop {
		@media (min-width: $tablet-landscape) { @content; }
	} @else if $range == big-desktop {
		@media (min-width: $desktop) { @content; }
	}
}

@mixin flex {
	display: -webkit-box;    //OLD - iOS 6-, Safari 3.1-6
	display: -webkit-flex;
	display: -moz-box;       //OLD - Firefox 19- (buggy but mostly works)
	display: -ms-flexbox;
	display: flex;           //iOS 7.1+, android 4.4+
}

//Flexbox Direction
@mixin flex-direction($value: row) {
	@if $value == row-reverse {
		-webkit-box-direction: reverse;
		-webkit-box-orient: horizontal;
	} @else if $value == column {
		-webkit-box-direction: normal;
		-webkit-box-orient: vertical;
	} @else if $value == column-reverse {
		-webkit-box-direction: reverse;
		-webkit-box-orient: vertical;
	} @else {
		-webkit-box-direction: normal;
		-webkit-box-orient: horizontal;
	}
	-webkit-flex-direction: $value;
	-moz-flex-direction: $value;
	-ms-flex-direction: $value;
	flex-direction: $value;
}

//Flexbox Justify Content
@mixin flex-justify($value: flex-start) {
	@if $value == flex-start {
		-webkit-box-pack: start;
		-ms-flex-pack: start;
	} @else if $value == flex-end {
		-webkit-box-pack: end;
		-ms-flex-pack: end;
	} @else if $value == space-between {
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
	} @else if $value == space-around {
		-ms-flex-pack: distribute;
	} @else {
		-webkit-box-pack: $value;
		-ms-flex-pack: $value;
	}
	-webkit-justify-content: $value;
	-moz-justify-content: $value;
	justify-content: $value;
}

//Flexbox Align Items
@mixin flex-align($value: stretch) {
	@if $value == flex-start {
		-webkit-box-align: start;
		-ms-flex-align: start;
	} @else if $value == flex-end {
		-webkit-box-align: end;
		-ms-flex-align: end;
	} @else {
		-webkit-box-align: $value;
		-ms-flex-align: $value;
	}
	-webkit-align-items: $value;
	-moz-align-items: $value;
	align-items: $value;
}

//Flexbox Grow
@mixin flex-grow($value: 0) {
	-webkit-box-flex: $value;
	-webkit-flex-grow: $value;
	-moz-flex-grow: $value;
	-ms-flex-positive: $value;
	flex-grow: $value;
}

//Flex-grow flex-shrink flex-basis (0 1 auto)
@mixin flex-value($grow: 1, $shrink: null, $basis: null) {
	$grow-box: $grow; //Box-Flex 속성에 사용할 변수 설정

	@if type-of($grow) == 'list' { //Box-Flex는 flex-grow 값만 지원하므로 목록의 첫 번째 항목을 반환
		$grow-box: nth($grow, 1);
	}

	-webkit-box-flex: $grow-box;
	-webkit-flex: $grow $shrink $basis;
	-moz-box-flex: $grow-box;
	-moz-flex: $grow $shrink $basis;
	-ms-flex: $grow $shrink $basis;
	flex: $grow $shrink $basis;
}

//Flexbox Wrap
@mixin flex-wrap($value: nowrap) {
	-webkit-flex-wrap: $value; //No Webkit Box fallback
	-moz-flex-wrap: $value;

	@if $value == nowrap {
		-ms-flex-wrap: none;
	} @else {
		-ms-flex-wrap: $value;
	}

	flex-wrap: $value;
}

//Gradient
@function is-direction($value) { //$value가 유효한 방향인지 테스트
	$is-keyword: index((to top, to right, to bottom right, to bottom, to bottom left, to left), $value);
	$is-angle: type-of($value) == 'number' and index('deg' 'grad' 'turn' 'rad', unit($value));

	@return $is-keyword or $is-angle;
}

@function legacy-direction($value) {
	@if is-direction($value) == false {
		@error "Cannot convert `#{$value}` to legacy syntax because it doesn't seem to be a direction.";
	}

	$conversion-map: (
		to top          : bottom,     //0deg
		to right        : left,       //90deg
		to bottom right : top left,   //
		to bottom       : top,        //180deg
		to bottom left  : top right,  //
		to left         : right,      //270deg
	);

	@if map-has-key($conversion-map, $value) {    //$conversion-map 맵에 $value 키가 있는지 검사
		@return map-get($conversion-map, $value); //$conversion-map 맵에서 $value 키로 값 검색
	}

	@return 90deg - $value; //@function의 값을 반환
}

@mixin linear-gradient($direction, $color-stops...) {
	@if is-direction($direction) == false {
		$color-stops: $direction, $color-stops;
		$direction: 180deg;
	}

	background: nth(nth($color-stops, 1), 1);                                        //CSS gradient가 지원되지 않는 경우 첫 번째 색상을 폴백으로 출력
	background: -webkit-linear-gradient(legacy-direction($direction), $color-stops); //-webkit-프리픽스가 붙은 값을 출력해, 각도를 구형 문법으로 변환
	background: linear-gradient($direction, $color-stops);                           //표준 선언 출력
}

@function strip-unit($number) {
	@if type-of($number) == 'number' and not unitless($number) {
		@return $number / ($number * 0 + 1);
	}

	@return $number;
}

@mixin ellipsis($display: inline-block, $max-width: 100%) {
	@if ($display) == 'inline-block' {
		max-width: $max-width;
	}

	display: $display;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

@mixin multiline-ellipsis($lines: 2, $font-size: $base-font-size, $line-height: $base-line-height, $height-value: null){
	@if unit($font-size) == 'rem' {
		$font-size-number : strip-unit($font-size) * 10 !global;
	} @else if unit($font-size) == 'px' {
		$font-size-number : strip-unit($font-size) !global;
	}

	overflow: hidden;
	display: block;
	display: -webkit-box;

	@if $height-value == 'fix' {
		height: ($font-size-number * $line-height * $lines) + px;
	} @else if $height-value == null {
		max-height: ($font-size-number * $line-height * $lines) + px;
	}

	font-size: $font-size;
	line-height: $line-height;
	text-overflow: ellipsis;
	word-break: break-all;
	overflow-wrap: break-word;
	-webkit-line-clamp: $lines;
	-webkit-box-orient: vertical;
}

@mixin color-set($font-color, $background-color,$border-color) {
	color: $font-color;
	background-color: $background-color;
	border-color: $border-color;
}

@mixin center($orientation: both, $position: absolute) {
	position: $position;

	@if ($orientation == both) {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	} @else if ($orientation == h) {
		left: 50%;
		transform: translate(-50%, 0);
	} @else if ($orientation == v) {
		top: 50%;
		transform: translate(0, -50%);
	}
}

@mixin scroll-y {
	overflow-x: hidden;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
}

@mixin bar($color: #d1d1d1, $height: 1.2rem) {
	margin: -1px .6rem 1px .2rem;
	display: inline-block;
	width: 1px;
	height: $height;
	background-color: $color;
	overflow: hidden;
	vertical-align: middle;
}

@mixin horizontalBar {
	content: '';
	display: block;
	height: 11px;
	border-top: 1px solid #e1e1e1;
	background-color: #f5f5f5;
}

@mixin thumb-fix($width: 8rem, $height: 7.2rem, $margin-right: null) {
	margin-right: $margin-right;
	position: relative;
	display: block;
	width: $width;
	height: $height;
	text-align: center;

	img {
		@include absolute(50%, null, null, 50%);
		max-width: 100%;
		max-height: 100%;
		transform: translate(-50%, -50%);
	}
}

//Swiper-pagination
@mixin swiper-pagination ($bottom: 0) {
	.swiper-pagination {
		bottom: $bottom;
		line-height: 0;

		.swiper-pagination-bullet {
			margin: 0 !important;
			padding: .5rem !important;
			width: auto;
			height: auto;
			line-height: 0;
			border-radius: 0;
			background: none;
			opacity: 1;
			outline: 0;
			vertical-align: top;

			&:before {
				content: '';
				display: inline-block;
				width: .6rem;
				height: .6rem;
				background: rgba(#fff, .9);
				box-shadow: 0 0 1px 1px rgba(0, 0, 0, .25);
				border-radius: 50%;
				vertical-align: top;
			}
		}

		.swiper-pagination-bullet-active {
			padding: .4rem;

			&:before {
				width: .6rem;
				height: .6rem;
				background: rgba($C-primary, .9);
				box-shadow: 0 0 1px 1px rgba(0, 0, 0, .25);
			}
		}
	}
}

@mixin iconTriangle($direction: 'up', $width: 20px, $height: 10px, $color: #000, $display: inline-block) {
	display: $display;
	width: $width;
	height: $height;

	&:before {
		content: '';
		display: block;

		@if ($direction == 'up') {
			border-bottom: $height solid $color;
			border-left: round($width/2) solid transparent;
			border-right: round($width/2) solid transparent;
		} @else if ($direction == 'down') {
			border-top: $height solid $color;
			border-left: round($width/2) solid transparent;
			border-right: round($width/2) solid transparent;
		} @else if ($direction == 'left') {
			border-top: round($height/2) solid transparent;
			border-bottom: round($height/2) solid transparent;
			border-right: $width solid $color;
		} @else if ($direction == 'right') {
			border-top: round($height/2) solid transparent;
			border-bottom: round($height/2) solid transparent;
			border-left: $width solid $color;
		}
	}
}
