@charset "UTF-8";

.policy-wrap .content .h-type {
    font-size: 1.5rem;
    line-height: 1
}

.policy-wrap .content .spot-area {
    padding: 1rem 16px 4rem;
    text-align: center
}

.policy-wrap .content .spot-area .btn-secondary {
    margin: 1.5rem auto 0;
    max-width: 24.8rem
}

.policy-wrap .content .policy-area {
    padding: 0 16px 2rem
}

.policy-wrap .content .policy-area:before {
    display: block;
    height: 11px;
    border-top: 1px solid #e1e1e1;
    background-color: #f5f5f5;
    margin-left: -16px;
    margin-right: -16px;
    margin-bottom: 2rem;
    content: ""
}

.policy-wrap .content .policy-area .policy-detail {
    margin: 1.5rem 0;
    border: 1px solid #eee;
    padding: 1rem;
}

.policy-wrap .content .policy-area .policy-detail .iframe {
    width: 100%;
    height: 500px;
    overflow-y: scroll;
    border: 0;
    margin: 0
}

.popup-online-guideline .title {
    margin-bottom: 1.7rem;
    padding-top: 1.4rem;
    display: block;
    font-size: 2rem;
    line-height: 1.3;
    font-weight: 400
}

.popup-online-guideline .dsc-list {
    margin-bottom: 2.1rem
}

.popup-online-guideline .btn-area {
    margin-bottom: 3rem;
    padding: 0 4rem
}

.popup-online-guideline .paragraph {
    margin-bottom: 1.8rem;
    font-size: 1.2rem;
    line-height: 1.5;
    color: #757575
}

.popup-online-guideline .db-btn-area {
    margin: 0 -.4rem 3rem
}

.popup-online-guideline .db-btn-area,
.popup-online-guideline .db-btn-area .btn-go {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center
}

.popup-online-guideline .db-btn-area .btn-go {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    justify-content: center;
    margin: 0 .4rem;
    padding: 0;
    width: 50%;
    height: 5rem;
    line-height: 1.3;
    color: #000;
    text-align: center;
    border: 1px solid #eee;
    border-radius: 0
}

.popup-online-guideline .banner-area {
    padding: 3rem 0;
    text-align: center
}

.popup-online-guideline .banner-area .img-logo {
    margin: 0 .3rem
}

.popup-online-guideline .banner-area .img-logo.v1 {
    width: 144px;
    height: 40px;
    background-position: 0 -84px;
    background-size: 460px 428px
}

.popup-online-guideline .banner-area .img-logo.v2 {
    width: 144px;
    height: 40px;
    background-position: -168px 0;
    background-size: 460px 428px
}

.popup-trouble-procedure .title {
    margin-bottom: 10px;
    display: block;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1.4;
    color: #333;
    letter-spacing: -1px
}

.popup-trouble-procedure .list-item {
    margin-top: 10px;
    padding-left: 15px;
    text-indent: -15px;
    font-size: 1.3rem;
    line-height: 1.5;
    color: #757575
}

.popup-marketing .h-type {
    margin: 2rem 0 1rem;
    font-size: 1.5rem;
    line-height: 1;
    color: #000;
    letter-spacing: -.025em
}

.popup-marketing .buyer-item .title {
    margin: 2rem 0 .9rem
}

.popup-marketing .buyer-item .form-group {
    margin: 1rem 0
}

.popup-marketing .dsc-list-wrap {
    margin: 3rem 0
}