@charset "UTF-8";

.family-site-area {
    margin-top: -.7rem
}

.family-site-area .family-site-item {
    display: block;
    border-bottom: 1px solid #e1e1e1
}

.family-site-area .family-site-item .link-block {
    display: block;
    padding: 0 2rem;
    font-size: 1.4rem;
    line-height: 5.5rem
}

.corp-info-area {
    padding: .8rem 16px 2rem
}

.corp-info-area .h-type {
    margin: 1rem 0 1.9rem
}

.corp-info-area .corp-info-list+.h-type {
    margin-top: 4.8rem
}

.corp-info-area .corp-info-item {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    -webkit-justify-content: space-between;
    -moz-justify-content: space-between;
    justify-content: space-between;
    min-height: 5.6rem;
    padding: .7rem 0;
    font-size: 1.4rem;
    line-height: 1.45;
    color: #000;
    border-top: 1px solid #e1e1e1
}

.corp-info-area .corp-info-item .tit {
    margin-right: 1rem;
    white-space: nowrap
}

.corp-info-area .corp-info-item .con {
    color: #666;
    text-align: right
}

.corp-info-area .corp-info-item .con .text-link {
    margin-left: .5rem;
    position: relative;
    top: -.1rem
}

.corp-info-area .award-list {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0 -.4rem
}

.corp-info-area .award-item {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    margin: 0 .4rem;
    display: block;
    width: 100%;
    border: 1px solid #e1e1e1
}

.corp-info-area .award-item .link-block {
    padding: 1rem .5rem 1rem 1rem;
    display: block;
    font-size: 1rem;
    line-height: 1.5;
    letter-spacing: -.025em;
    color: #666;
    overflow: hidden
}

.corp-info-area .award-item .link-block .ico {
    margin-right: 1rem;
    float: left
}

.corp-info-area .award-item .link-block .ico.award1 {
    width: 50px;
    height: 32px;
    background-position: -372px -279px;
    background-size: 460px 428px
}

.corp-info-area .award-item .link-block .ico.award2 {
    width: 50px;
    height: 32px;
    background-position: -316px -224px;
    background-size: 460px 428px
}