@charset "UTF-8";

//$base- Default variables for reset
//$F-    Font-Related Variables
//$C-    Color related variables
//$G-    Global variable

// Layout
$G-layout-max-width   : 600px;
$G-layout-margin-left : 16px;
$G-layout-margin-right: 16px;

// Img Path
$G-path-local:  '../img/';                                 //local
$G-path-static: 'http://cdnm.shop.tworld.co.kr/public/';  //static
$G-path-img:    $G-path-local;

// Font
$F-family-eng:  HelveticaNeue, Helvetica, sans-serif;
$F-family-kor:  "Noto Sans", "Noto Sans CJK KR", AppleSDGothicNeo-Medium, sans-serif;
$F-family-base: $F-family-kor;

// Font - Base
$root-font-size:   10px;
$base-font-family: $F-family-base;
$base-font-size:   1.4rem;
$base-line-height: 1.25em;

// Font - Weight
$F-weight-light:  300;
$F-weight-normal: 400;
$F-weight-bold:   700;

// Color
$C-extra: 	       #e6f2fb; //[StyleGuide] 연한하늘(bg)
$C-secondary:      #3b98e6; //[StyleGuide] 하늘 - 버튼 폰트 및 버튼 bg용
$C-secondary-dark: #178bce; //[StyleGuide] 진한하늘 -text용
$C-primary:        #f76252; //[StyleGuide] 빨강
$C-primary-dark:   #ef4b49; //[StyleGuide] 진한빨강 - Tab용

// z-index
$z-index: (
	gnb-drawer     : 10070,
	overlay-layer  : 10060,
	overlay-dimmed : 10050,
	layer          : 10040,
	dimmed         : 10030,
	sticky		   : 10010,
	search-trigger : 10000,
	header         : 10000,
	header-lnb     : 999
	//go-top             : 10000,
	//navigation         : 100,
	//footer             : 90,
	//triangle           : 60,
	//navigation-rainbow : 50,
	//share-type         : 41,
	//share              : 40,
	//default            : 1,
	//below              : -1,
);